import Vue from 'vue'
import Router from 'vue-router'
//  vue-scrollto
import VueScrollTo from 'vue-scrollto'
//
const Home = () =>import('@/components/Home')
//
const Register = () => import('@/components/Auth/Register')

const Login = () => import('@/components/Auth/Login')
//
//
const User = () => import('@/components/Profile/User')
const Followers = () => import('@/components/Profile/Followers')
const Following = () => import('@/components/Profile/Following')
const Overview = () => import('@/components/Profile/Overview')
const Posts = () => import('@/components/Profile/Posts')

//
const Settings = () => import('@/components/Settings/Settings')
const Profile = () => import('@/components/Settings/Profile')
const Account = () => import('@/components/Settings/Account')
const Email = () => import('@/components/Settings/Email')
const Notiffication = () => import('@/components/Settings/Notiffication')

//

//
const FollowingPosts = () => import('@/components/Notifications/FollowingPosts')
const CommentsLikes = () => import('@/components/Notifications/CommentsLikes')
const Notifications = () => import('@/components/Notifications/Notifications')
const RecivedComments = () => import('@/components/Notifications/RecivedComments')
const RecivedMessages = () => import('@/components/Notifications/RecivedMessages')

//
import PostDetail from '@/components//PostDetail'

//ping
const Ping = () => import('@/components/Ping')


Vue.use(Router)
// history +
// vue-route
// #xxx -
// 
const scrollBehavior = function(to,from,savePosition){
  if(savePosition){
    if(to.hash){
      setTimeout(() => {
        VueScrollTo.scrollTo(to.hash,200)
      }, 1000);
      return {x:0,y:0}
    }
    return savePosition
  }else{
    if(to.hash){
      //#xxx
      // return new Promise((resolve,reject)=>{

      //   setTimeout(() => {
      //     resolve({selector:to.hash})
      //   }, 1000)
      // })
      setTimeout(() => {
        VueScrollTo.scrollTo(to.hash,200)
      }, 1000);
      return;
    }
    if(to.name == 'UserFollowers' && to.query.timestamp){
      // - 
      return {}
    }
    return {x:0,y:0}
  }
}

const router = new Router({
  mode: 'history',
  scrollBehavior,
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/ping',
      name: 'Ping',
      component: Ping
    },{
      path:'/user/:id',
      children:[
        {path:'',component:Overview},
        {path: 'overview', name: 'UserOverview', component: Overview },
        {path:'followers',name:'UserFollowers',component:Followers},
        {path:'following',name:'UserFollowing',component:Following},
        {path:'posts',name:'Posts',component:Posts}
      ],
      component:User,
      meta:{
        requiresAuth:true
      }
    },{
      path:'/post/:id',
      name:'PostDetail',
      component:PostDetail
    },{
      path:'/settings',
      component:Settings,
      children:[
        {path:'',component:Profile},
        {path:'profile',name:'SettingProfile',component:Profile},
        {path:'account',name:'SettingAccount',component:Account},
        {path:'email',name:'SettingEmail',component:Email},
        {path:'notiffication',name:'SettingNotiffication',component:Notiffication}
      ]
    },{
      path:'/notifications',
      component:Notifications,
      children:[
        {path:'',component:RecivedComments},
        {path:'comments',name:'RecivedComments',component:RecivedComments},
        {path:'messages',name:'RecivedMessages',component:RecivedMessages},
        {path:'follows',name:'Follows',component:Followers},
        { path: 'commentslikes', name: 'CommentsLikes', component: CommentsLikes },
        { path: 'following-posts', name: 'FollowingPosts', component: FollowingPosts }
      ],
      meta:{
        requiresAuth:true
      }
    }

  ]
})

router.beforeEach((to, from, next) => {
  if(to.matched.length===0){
    Vue.toasted.error("...")
    router.back()
    return;
  }
  const token = window.localStorage.getItem('madblog-token')
  //some
  //  es6sometruetrueeverytruetrue
  //  recordmatched
  //   -> 
  //if
  //  metapath:'/'
  // 
  //url
  //  urlurl
  if (to.matched.some(record => record.meta.requiresAuth) && (!token || token === null)) {
    next({
      path: '/login',
      query: { redirect: to.fullPath }
    })
  } else if (token && to.name == 'Login') {
    // 
    // next({
    //   path: from.fullPath
    // })
    next(false)
  } else {
    //
    next()
  }
})

export default router


